.registation h3 {
    line-height: 1.5;
    font-size: 30px;
    color: #000;
    font-weight: 300;
    margin-top: 4em;
    font-family: "Playfair Display", Georgia, serif;
}

.registation .form {
    width: 90%;
    margin-top: 4rem;
    padding: 1.5rem;
    background-color: #fafafa
}

.registation .form-group {
    margin-bottom: 1rem;
    width: 100%;
}

.registation .form-group label {
    margin-bottom: 0.5rem;
    color: #737373;
    font-weight: 400;
    display: inline-block;
}

.registation .form-group .form-controls {
    height: 52px !important;
    background: #fff !important;
    color: #000 !important;
    font-size: 18px;
    border-radius: 0px;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    display: block;
    border: 1px solid #ced4da;
    width: 100%;
    padding: 0.375rem 0.75rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
    line-height: 1.5;
    ;
}

.registation .form-group textarea.form-controls {
    height: inherit !important;
}

.registation .form-group .btns-registation {
    background: #ee76ad;
    border: 1px solid #ee76ad;
    color: #fff;
    border-radius: 30px;
    padding-left: 1.5rem !important;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: 1rem;
    line-height: 1.5;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
}

.registation .form-group .btns-registation:hover {
    background-color: #fff;
    border: 1px solid #ee76ad;
    color: #ee76ad;
}

.registation .form-group.button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.registation .form-group.button span {
    margin-left: 10px;
    width: 1.3em;
    height: 1.3em;
}

.error {
    color: red;
    font-size: 20px;
    font-family: 'poppins', sans-serif;
    font-weight: 500;
    margin-top: 5px;
}

#regular-password {
    position: absolute;
    right: 8px;
    font-size: 20px;
    top: 62%;
}

.google {
    padding: 2px 10px;
    display: inline-block;
}

.google .google-content {
    max-width: 230px;
    min-width: 230px;
    width: 250px;
    border-radius: 20px;
    transition: background-color .218s, border-color .218s;
    -webkit-user-select: none;
    -webkit-appearance: none;
    background-color: #fff;
    background-image: none;
    border: 1px solid #dadce0;
    color: #3c4043;
    cursor: pointer;
    font-family: "Google Sans", arial, sans-serif;
    font-size: 14px;
    height: 40px;
    letter-spacing: 0.25px;
    outline: none;
    overflow: hidden;
    padding: 0 12px;
    position: relative;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
}

.google-item {
    flex-direction: row;
    justify-content: space-between;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    height: 100%;
    position: relative;
    width: 100%;
}

.google-item span {
    color: #000;
    font-size: 17px;
}
.linkes{
    font-size: 17px;
    cursor: pointer;
    color: #ee76ad;
}
.change-link:hover{
    text-decoration: underline;
}
.forgot-link:hover{
    text-decoration: underline;
}