.login-succes{
    position: fixed;
    left: -100%;
    top: 20px;
    width: 400px;
    padding: 12px;
    z-index: 10000;
    background-color: rgba(0, 0, 0, 0.744);
    border-radius: 12px;
    transition: left 0.4s ease-in-out;
}
.login-succes.show{
    left: 10px;
}
.login-succes .image{
    width: 50px;
    height: 50px;
}
.login-succes .image img{
    border-radius: 50%;
}
.login-succes .name-email {
    margin-left: 12px;
    color: #fff;
}
.login-succes .progress-width{
    width: 100%;
    height: 5px;
    border-radius: 4px;
    margin-top: 12px;
    background: #888888;
}
.login-succes .progress-width .child-progress{
    background: yellowgreen;
    height: 100%;
    border-radius: 4px;
}