.gallery {
    width: 50%;
    background-color: white;
    display: flex;
    flex-direction: column;
}

.data-conteiner .wrapper {
    width: 45%;
    margin: 20px 12px;
}

.text {
    padding-top: 1.5rem;
    font-family: "Work Sans", Arial, sans-serif;
    font-size: 18px;
    line-height: 1.8;
    font-weight: 300;
    color: #737373;
    padding-bottom: 1.5rem;
}

.text .heading {
    font-size: 28px;
    margin-bottom: 16px;
    font-weight: 400;
    line-height: 1.5;
    font-family: "Playfair Display", Georgia, serif;
    color: #000;
}

.text p {
    font-size: 20px;
}

.data-conteiner .wrapper:nth-child(1).active {
    opacity: 0;
    transform: translateY(50px);
    animation: anim 1.5s forwards;
}

.data-conteiner .wrapper .image {
    overflow: hidden;
}

.data-conteiner .wrapper .image img {
    transition: all 0.2s ease-in-out;
}

.data-conteiner .wrapper .image img:hover {
    transform: scale(1.2);
}

.data-conteiner .wrapper:nth-child(2).active {
    opacity: 0;
    transform: translateY(50px);
    animation: anim 1.5s forwards;
}

.data-conteiner .wrapper:nth-child(3).active {
    opacity: 0;
    transform: translateY(50px);
    animation: anim 1.5s forwards;
}

.data-conteiner .wrapper:nth-child(4).active {
    opacity: 0;
    transform: translateY(50px);
    animation: anim 2s forwards;
}

.data-conteiner .wrapper:nth-child(5).animation {
    opacity: 0;
    transform: translateY(250px);
    animation: fade 1s forwards;
}

.data-conteiner .wrapper:nth-child(6).animation {
    opacity: 0;
    transform: translateY(250px);
    animation: fade 0.8s forwards;
}

@keyframes fade {
    0% {
        opacity: 0;
        transform: translateY(250px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes anim {
    50% {
        opacity: 0;
        transform: translateY(50px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.data-conteiner .wrapper:nth-child(7).lastanimation {
    opacity: 0;
    transform: translateY(250px);
    animation: lastanimation 0.9s forwards;
}

.data-conteiner .wrapper:nth-child(8).lastanimation {
    opacity: 0;
    transform: translateY(250px);
    animation: lastanimation 0.9s forwards;
}
@keyframes lastanimation {
    50% {
        opacity: 0;
        transform: translateY(250px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}