.about-conteiner{
    width: 50%;
    padding: 30px;
}
.about-conteiner .about-row{
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}
.about-conteiner .about-row .image{
    width: 301.5px;
    height: auto;
}
.about-conteiner .about-row .image.fade{
    animation: fade .5s forwards;
}
.about-conteiner .about-row .about-description{
    margin-left: 40px;
    width: 38%;
}
.about-conteiner .about-row .about-description.fade{
    animation: fade .5s forwards;
}
.about-conteiner .about-bottom.fade{
    animation: fade .5s forwards;
}
.about-conteiner .about-bottom.fade .icons-link li.fade{
    animation: fade .5s forwards;
}
.about-conteiner .about-bottom.fade .icons-link li i{
    position: absolute;
    font-size: 26px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
.about-conteiner .about-bottom.fade .icons-link li {
    margin: 0 10px 10px 0;
    border: none;
    display: inline-block;
}
.about-conteiner .about-bottom.fade .icons-link li a{
    height: 60px;
    width: 60px;
    display: block;
    border: none;
    float: left;
    background: black;
    border-radius: 50%;
    position: relative;
    color: #fff;
}
@keyframes fade {
    0%{
        opacity: 0;
        visibility: hidden;
        -webkit-transform: translate3d(0, 40px, 0);
        transform: translate3d(0, 40px, 0);
    }
    100%{
        visibility: visible;
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}
.about-conteiner .about-row .about-description.fade h2{
    line-height: 1.5;
    color: #000;
    font-weight: 300;
    font-family: "Playfair Display", Georgia, serif;
    font-size: 2rem;
    z-index: 100;
    white-space: wrap;
}
.about-conteiner .about-row .about-description p{
    font-size: 18px;
    font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-weight: 400;
    line-height: 40px;
}
.about-conteiner .about-bottom.fade{
    margin-top: 3rem;
}
.about-conteiner .about-bottom.fade p{
    font-size: 20px;
    line-height: 40px;
    font-weight: 400;
}
.about-conteiner .about-bottom.fade h3{
    margin-top: 3rem;
    line-height: 1.5;
    color: #000;
    font-weight: 300;
    font-family: "Playfair Display", Georgia, serif;
    font-size: 30px;
}