.add-comment{
    display: flex;
    margin-top: 12px;
}
.add-comment .image {
    width: 60px;
    height: 60px;
}
.coment-conteiner img{
    border-radius: 50%;
}
.add-comment .name{
    margin-left: 10px;
    font-weight: 600;
    color: black;
}
.add-btns{
    padding: 12px 20px;
    background: green;
    color: #fff;
    margin-right: 6px;
    border-radius: 6px;
}
.cancel-btns{
    padding: 12px 20px;
    border-radius: 6px;
    background: red;
    color: #fff;
}
.update-delete{
    display: flex;
    align-items: center;
    opacity: 0;
    transition: all 0.2s ease-in-out;
    transform: scale(0);
}
.commnets-count:hover .update-delete{
    opacity: 1;
    transform: scale(1);
}


.update-delete button{
    padding: 12px;
    border: 1px solid gainsboro;
}