.video-controls{
    position: absolute;
    z-index: 1;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.video-controls .controls{
    width: 80px;
    height: 80px;
    line-height: 80px;
    background: #ee76ad;
    color: #fff;
    cursor: pointer;
    font-size: 18px;
    text-align: center;
    border-radius: 50%;
    animation: pulse 2s infinite;
}

@keyframes pulse {
    0%{
        box-shadow: 0 0 0 0 rgba(238, 118, 173, 0.4);
    }
    70%{
        box-shadow: 0 0 0 30px rgba(238, 118, 173, 0);
    }
    100%{
        box-shadow: 0 0 0 0 rgba(238, 118, 173, 0);
    }
}