.model-conteiner {
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.model {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.model-conteiner .wrapper {
    width: 455px;
    position: relative;
    height: 400px;
}

.model-conteiner .wrapper:nth-child(1).fadein {
    animation: fadein .5s forwards;
}
.model-conteiner .wrapper:nth-child(2).fadein {
    animation: fadein .5s forwards;
}
.model-conteiner .wrapper:nth-child(3).fadein {
    animation: fadein .5s forwards;
}
.model-conteiner .wrapper:nth-child(4).fadein {
    animation: fadein .5s forwards;
}
.model-conteiner .wrapper:nth-child(5).fadein {
    animation: fadein .5s forwards;
}
.model-conteiner .wrapper:nth-child(6).fadein {
    animation: fadein .5s forwards;
}
.model-conteiner .wrapper:nth-child(7).animated{
    animation: fadein .5s forwards;
}
.model-conteiner .wrapper:nth-child(8).animated{
    animation: fadein .5s forwards;
}
.model-conteiner .wrapper:nth-child(9).anim{
    animation: fadein .5s forwards;
}
.model-conteiner .wrapper:nth-child(10).anim{
    animation: fadein .5s forwards;
}
@keyframes fadein {
    0% {
        opacity: 0;
        visibility: hidden;
        -webkit-transform: translate3d(0, 40px, 0);
        transform: translate3d(0, 40px, 0)
    }

    100% {
        visibility: visible;
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}

.model-conteiner .wrapper a {
    border: none;
}

.model-conteiner .wrapper .image {
    width: 455px;
    height: 400px;
}

.model-conteiner .wrapper .text {
    position: absolute;
    bottom: 0;
    padding: 1.5rem;
    z-index: 1;
    display: block;
    width: 100%;
    background: rgba(0, 0, 0, 0.9);
    opacity: 0;
    transition: 0.3s;
}

.model-conteiner .wrapper:hover .text {
    opacity: 1;
}

.model-conteiner .wrapper .text span {
    font-size: 14px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.7);
    border: 1px solid rgba(255, 255, 255, 0.7);
    padding: 4px;
}

.model-conteiner .wrapper .text h3 {
    color: #fff;
    font-size: 1.75rem;
    line-height: 1.5;
    font-weight: 300;
    font-family: "Playfair Display", Georgia, serif;
}
.local-dates span{
    color: #ccc;
    font-size: 18px;
    margin: 0 6px;
}
.delete-img{
    position: absolute;
    right: 15px;
    top: 15px;
    color: #fff;
    cursor: pointer;
    font-size: 3rem;
    display: none;
}
.model-conteiner .wrapper:hover .delete-img{
    display: block;
}
.add-img{
    padding: 13px 50px;
    border: none;
    color: #fff;
    background: orangered;
    border-radius: 16px;
}