.post-conteiner{
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 30px;
}
.post-conteiner > .wrapper{
    width: 100%;
}
.post-conteiner > .wrapper .posts-title h2{
    margin-bottom: 1rem;
    line-height: 1.5;
    color: #000;
    font-weight: 300;
    font-family: "Playfair Display", Georgia, serif;
    font-size: 2rem;
}
.post-conteiner > .wrapper .post-text{
    font-size: 25px;
    white-space: wrap;
    font-family: "Playfair Display", Georgia, serif;
    line-height: 50px;
    font-weight: normal;
    color: #737373;
}
.post-conteiner > .wrapper .image{
    margin-top: 15px;
}
.post-conteiner > .wrapper .description{
    font-size: 25px;
    font-family: "Playfair Display", Georgia, serif;
    line-height: 50px;
    color: #737373;
    font-weight: normal;
}
.post-conteiner .descriptions p{
    font-size: 20px;
    font-weight: 600;
    margin-top: 25px;
    font-weight: normal;
    line-height: 40px;
}
.link{
    text-transform: uppercase;
    text-decoration: none;
    display: inline-block;
    padding: 4px 10px;
    margin-bottom: 7px;
    margin-right: 4px;
    border-radius: 4px;
    color: #000;
    margin: 0 8px;
    border: 1px solid #ccc;
    font-size: 11px;
    transition: .3s all ease;
}
.link:hover{
    border: 1px solid black;
}
.post{
    padding-top: 4rem;
}
.post .commnent-conteiner{
    display: flex;
    /* align-self: center; */
}

.post .commnent-conteiner h2{
    line-height: 1.5;
    color: #000;
    font-weight: 300;
    font-family: "Playfair Display", Georgia, serif;
    margin-bottom: 1.5rem;
    font-size: 22px;
    line-height: 1.8;
    font-weight: 300;
}
.post .commnent-conteiner .description{
    margin-left: 8px;
}
.commnets{
    margin-top: 5em;
}
.commnets .image{
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    width: 160px;
    height: 150px;
    border-radius: 50%;
    margin-top: 6px;
    margin-right: 0.5rem;
}
.commnets .image img{
    border-radius: 50%;
}
.commnets .counts{
    margin-bottom: 3rem;
    line-height: 1.5;
    color: #000;
    font-size: 2em;
    font-weight: 300;
    font-family: "Playfair Display", Georgia, serif;
}
.name-description {
    width: 100%;
}
.name-description h2{
    line-height: 1.5;
    color: #000;
    font-weight: 300;
    font-family: "Playfair Display", Georgia, serif;
    font-size: 20px;
}
.name-description .date{
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: .1em;
    color: #ccc;
    margin-top: 5px;
}
.name-description p{
    margin-top: 1em;
    color: #737373;
    font-size: 20px;
    line-height: 40px;
    white-space: wrap;
}
.name-description button{
    padding: 5px 10px;
    background: #e6e6e6;
    color: #000;
    text-transform: uppercase;
    
    font-size: 11px;
    letter-spacing: .1em;
    font-weight: 400;
    border-radius: 4px;
    transition: .3s all ease;
}
.name-description button:hover{
    background-color: #000;
    color: #fff;
}
