header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    padding: 2em 1em;
    width: 50vw;
}

.body-conteiner {
    display: flex;
    justify-content: end;
}

.navbar {
    position: absolute;
    top: 0;
    background: linear-gradient(45deg, #29bae4 0%, #ee76ad 100%);
    z-index: 1002;
    transition: all 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    bottom: 0;
    left: 0;
    padding: 0;
    visibility: hidden;
    opacity: 0;
    transform: scale(0.1);
    width: 50vw;
    height: 100%;
    overflow: scroll;
}

.navigation-bar {
    position: absolute;
    top: 30%;
    right: 1%;
}

.navigation-bar .list li {
    padding: 0;
    margin-top: 6px;
    list-style: none;
    font-weight: 400;
    font-size: 30px;
}

.navigation-bar .list li a {
    display: block;
    color: white;
    padding: 5px 0;
    transition: .3s all ease;
}

.navigation-bar .list li a span {
    font-size: 30px;
    color: white;
    position: relative;
    padding: 0;
}

.navigation-bar .list li a span::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: 0;
    left: 0;
    transition: all 0.3s ease-in-out 0s;
    transform: scaleX(0);
    visibility: hidden;
    background-color: #fff;
}

.navigation-bar .list li a.active span::before {
    visibility: visible;
    transform: scaleX(1);
}

.navaigation-bottom {
    margin-top: 40px;
}

.navaigation-bottom p {
    color: rgba(255, 255, 255, 0.7);
    font-size: 18px;
    line-height: 35px;
}

.navigation-bar .list li a span:hover::before {
    visibility: visible;
    transform: scaleX(1);
}

.navbar.show {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}

.navbar.show .list {
    padding-left: 3rem;
    padding-right: 3rem;
    position: relative;
    width: 100%;
}

.navbar.show .close {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    position: absolute;
    top: 30px;
    right: 20px;
    z-index: 1;
}

.navbar.show .close.active {
    transform: scale(0.1);
    opacity: 0;
    animation: anim 1s forwards;
}

@keyframes anim {
    0% {
        visibility: hidden;
    }

    80% {
        transform: scale(0.1);
        opacity: 0;
        visibility: hidden;
    }

    100% {
        transform: scale(1);
        opacity: 1;
        visibility: visible;
    }
}

.navbar.show .liner {
    width: 40px;
    height: 2px;
    background: #fff;
}

.navbar.show .liner:nth-child(1) {
    transform: rotate(48deg) translate(4px, 3px);
}

.navbar.show .liner:nth-child(2) {
    opacity: 0;
}

.navbar.show .liner:nth-child(3) {
    transform: rotate(-42deg) translate(1px, 0);
}

header .wrapper .logo {
    font-size: 20px;
    text-transform: uppercase;
    color: #fff;
    font-weight: 400;
    letter-spacing: 5px;
    display: block;
    text-align: center;
    position: relative;
    font-family: "Work Sans", Arial, sans-serif;
}

header .wrapper .logo a:hover {
    color: #fff;
}

.liner {
    width: 40px;
    height: 2px;
    color: #fff;
    font: bold 14px / .4 Helvetica;
    text-transform: uppercase;
    text-indent: -55px;
    background: #fff;
    transition: all .2s ease-in-out;
}

.slider-carusel {
    display: flex;
    transition: transform 0.3s ease;
    width: 50vw;
    height: 100%;
}

.slider-carusel .image {
    width: 50vw;
    height: 100%;
}

.slider-conteiner {
    width: 50vw;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.conteiner {
    width: 50vw;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
}

.slider-conteiner .owl-item {
    width: 50vw !important;
    height: 100% !important;
    display: none;
    animation: showslider 1.5s forwards;
}

.slider-conteiner .owl-item.active {
    display: block;
}

@keyframes showslider {
    5% {
        opacity: 0;
        transform: scale(0.9);
    }

    100% {
        opacity: 1;
        transform: scale(1);
    }
}


.slider-text {
    font-size: 40px;
    color: #fff;
    line-height: 1.3;
    font-weight: 400;
    position: absolute;
    width: 50%;
    bottom: 30%;
    letter-spacing: 1px;
    z-index: 100;
    padding-left: 1em;
    font-family: "Playfair Display", Georgia, serif;
}

.slider-text.fadein {
    animation: fade 1.5s forwards;
}

@keyframes fade {
    10% {
        opacity: 0;
        visibility: hidden;
        -webkit-transform: translate3d(0, 100px, 0);
        transform: translate3d(0, 100px, 0);
    }

    100% {
        visibility: visible;
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}

.slider-text h1 {
    white-space: wrap;
}

.slider-navigation {
    position: absolute;
    align-items: center;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, -50%);
}

.slider-navigation .owl-doot {
    width: 18px;
    height: 18px;
    margin: 5px;
    border-radius: 50%;
    cursor: pointer;
    background: none;
    transition: .3s all ease;
    display: inline-block;
    border: 2px solid rgba(255, 255, 255, 0.5);
    outline: none !important;
    position: relative;
}

.slider-navigation .owl-doot span {
    position: absolute;
    width: 12px;
    height: 12px;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 50% !important;
    left: 0%;
    top: 2px;
    display: block;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    margin: 6px 7px;
}

.slider-navigation .owl-doot.active {
    border: 2px solid white;
}

.slider-navigation .owl-doot span.active {
    background: #fff;
}