.loading-conteiner {
    position: relative;
    height: 100vw;
    width: 100%;
    background-color: white;
    z-index: 1000;
}
.loading-conteiner .loading{
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.loading-conteiner .spinner {
    background-color: red;
    width: 100px;
    position: relative;
    height: 100px;
    border-radius: 50%;
    background: conic-gradient(red, transparent);
    animation: rotate 1s linear infinite;
}

.loading-conteiner .spinner::before,
.loading-conteiner .spinner::after {
    content: "";
    position: absolute;
    border-radius: 50%;
}

.loading-conteiner .spinner::before {
    width: 80px;
    height: 80px;
    top: 10px;
    left: 10px;
    background-color: #fff;
}

.loading-conteiner .spinner::after {
    width: 10px;
    height: 10px;
    background-color: red;
    top: 0;
    left: 45px;
}

.loading-conteiner p {
    font-family: tahoma;
    color: red;
    text-align: center;
    margin-top: 18px;
    font-weight: bold;
}

@keyframes rotate {
    from {
        transform: rotateZ(0);
    }

    to {
        transform: rotateZ(360deg);
    }
}