.video-conteiner{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background-color: black;
}
.video-conteiner .video-close{
    position: absolute;
    right: 15px;
    top: 15px;
    font-size: 30px;
    color: #333;
    cursor: pointer;
}
.video-conteiner .video-close:active{
    transform: translateY(2px);
}
.video-conteiner .video-item{
    width: 80%;
    height: 100%;
    margin: 0 auto;
    padding: 15px;
    background: #fff;
    position: relative;
}
.video-conteiner .video-item .video{
    width: 100%;
    height: 100%;
}
.video-conteiner .video-play{
    width: 60px;
    height: 60px;
    border-radius: 5px;
    background: black;
    cursor: pointer;
    color: #fff;
    text-align: center;
    line-height: 62px;
}
.video-conteiner .video-div{
    margin-left: 12px;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 3px;
    background: #000;
    position: relative;
    padding: 8px 20px;
}
.duration{
    position: absolute;
    top: -5px;
    background: #fff;
    border-radius: 3px;
    font-size: 12px;
    color: #000;
}
.video-conteiner .video-div .progress{
    width: 100%;
    height: 4px;
    background: #333;
    border-radius: 6px;
    cursor: pointer;
    cursor: pointer;
}
.video-conteiner .video-settings{
    margin-left: 1rem;
    white-space: nowrap;
}
.video-conteiner .video-settings i{
    font-size: 17px;
    color: #fff;
    margin: 0 10px;
}
.timer{
    width: 350px;
    border-radius: 12px;
    position: absolute;
    bottom: 11%;
    background-color: black;
}
.timer .video-timer{
    text-align: center;
    color: #fff;
}