.user-img .image {
    width: 35px;
    height: 35px;
}

.user-img {
    border-radius: 50%;
    border: 1px solid #9a9898;
}

.user-name {
    margin-left: 12px;
    color: #fff;
    font-size: 18px;
    font-weight: normal;
    font-family: 'poopins', sans-serif;
}

.user-img .image img {
    border-radius: 50%;
}

.drowdown-menu {
    position: absolute;
    top: 120px;
    right: 10px;
    padding: 10px 20px;
    background: #fff;
    width: 200px;
    box-sizing: 0 5px 25px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    transition: 0.5s;
    visibility: hidden;
    opacity: 0;
}

.drowdown-menu.active {
    top: 150px;
    visibility: visible;
    opacity: 1;
}

.drowdown-menu::before {
    content: "";
    position: absolute;
    top: -5px;
    right: 28px;
    width: 20px;
    height: 20px;
    background: #fff;
    transform: rotate(45deg);
}

.profile-header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.drowdown-menu .profile-header h3 {
    width: 100%;
    font-size: 18px;
    padding: 20px 0;
    font-weight: 500;
    color: #555;
}

.drowdown-menu .profile-header .image {
    width: 70px;
    height: 70px;
}

.drowdown-menu .profile-header .image img {
    border-radius: 50%;
}

.drowdown-menu .profile-header span {
    font-size: 14px;
    color: #cecece;
    font-weight: 300;
}

.drowdown-menu ul li {
    list-style: none;
    padding: 16px 0;
    border-top: 1px solid rgba(0, 0, 0, 0.05);
    display: flex;
    align-items: center;
}

.drowdown-menu ul li img {
    max-width: 20px;
    margin-right: 10px;
    opacity: 0.5;
    transition: 0.5s;
}

.drowdown-menu ul li img:hover {
    opacity: 1;
}

.drowdown-menu ul li a {
    display: inline-block;
    text-decoration: none;
    color: #555;
    font-weight: 500;
    transition: 0.5s;
}

.drowdown-menu ul li a:hover {
    color: #ff5d94;
}