.language{
    margin-left: 15px;
    padding: 13px;
    width: 380px;
    position: relative;
}
.language .language-flag .image{
    width: 30px;
    height: 30px;
}
.language .language-header{
    padding: 12px;
    border-radius: 5px;
    width: 50%;
    box-shadow: 0px 0px 0px 1px #ccc;
    cursor: pointer;
   border: 2px solid #4b4a4a;
}
.language .language-header span{
    color: #fff;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.language .language-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 12px;
    margin-top: 0.5rem;
    cursor: pointer;
}
.language .language-content{
    background-color: white;
    width: 50%;
    border-radius: 5px;
    box-shadow: 0.5px 0.5px 8px #4b4a4a;
    position: absolute;
    margin-top: 5px;
    transform: scale(0);
    transition: all 12ms ease-in;
}
.language .language-content.active{
    transform: scale(1);
}