.travel-conteiner {
    padding: 30px;
    width: 50vw;
}

.travel-conteiner .travel-item {
    display: flex !important;
    margin-bottom: 3em;
}

.travel-conteiner .travel-item .image {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    width: 190px;
    height: 180px;
    border-radius: 50%;
    margin-top: 6px;
    margin-right: 0.5rem;
}

.travel-conteiner .travel-item .text {
    margin-left: 15px;
}

.travel-conteiner .travel-item:nth-child(1).animated,
.travel-conteiner .travel-item:nth-child(2).animated,
.travel-conteiner .travel-item:nth-child(3).animated,
.travel-conteiner .travel-item:nth-child(4).animated {
    animation: fade .5s forwards;
}

.travel-conteiner .travel-item:nth-child(5).fade,
.travel-conteiner .travel-item:nth-child(6).fade,
.travel-conteiner .travel-item:nth-child(7).fade,
.travel-conteiner .travel-item:nth-child(8).fade {
    animation: fadein .5s forwards;
}

@keyframes fadein {
    0% {
        opacity: 0;
        visibility: hidden;
        -webkit-transform: translate3d(0, 40px, 0);
        transform: translate3d(0, 40px, 0);
    }

    100% {
        visibility: visible;
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}


@keyframes fade {
    0% {
        opacity: 0;
        visibility: hidden;
        -webkit-transform: translate3d(0, 40px, 0);
        transform: translate3d(0, 40px, 0);
    }

    100% {
        visibility: visible;
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}

.travel-conteiner .travel-item .text h3 {
    margin-bottom: 2em;
    font-size: 25px;
    color: #000;
    font-weight: 500;
    white-space: nowrap;
    font-family: 'Times New Roman', Times, serif;
}

.travel-conteiner .travel-item .text .date {
    margin-top: 15px;
}