.fashion-conteiner {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    padding: 30px;
}

.fashion-conteiner>.post-wrapper {
    width: 80%;
    margin-bottom: 3em;
}

.fashion-conteiner>.post-wrapper:nth-child(1).fadein {
    animation: fadeInUp .5s forwards;
}
.fashion-conteiner>.post-wrapper:nth-child(2).fadein {
    animation: fadeInUp .8s forwards;
}
.fashion-conteiner > .post-wrapper:nth-child(3).animation{
    animation: fadeInUp .8s forwards;
}
.fashion-conteiner > .post-wrapper:nth-child(4).fade{
    animation: animshow .8s forwards;
}
.fashion-conteiner > .post-wrapper:nth-child(5).fade{
    animation: animshow .8s forwards;
}
.fashion-conteiner > .post-wrapper:nth-child(6).fade{
    animation: animshow .8s forwards;
}

@keyframes animshow {
    10% {
        opacity: 0;
        visibility: hidden;
        transform: translateY(70px);
    }
    100%{
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeInUp {
    8% {
        opacity: 0;
        visibility: hidden;
        transform: translateY(70px);
    }
    100%{
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
    }
}

.fashion-conteiner>.post-wrapper .image {
    width: 100%;
    position: relative;
    height: 480px;
}

.fashion-conteiner>.post-wrapper .fashion-text {
    display: flex !important;
}

.fashion-conteiner>.post-wrapper .fashion-text .date {
    width: 120px;
    text-align: right;
    padding-top: 1.5rem;
}

.fashion-conteiner>.post-wrapper .fashion-text .date p {
    display: block;
    font-size: 16px;
    line-height: 1.8;
    font-weight: 300;
    color: #737373;
    font-family: "Work Sans", Arial, sans-serif;
}

.fashion-conteiner>.post-wrapper .content {
    width: calc(100% - 120px);
    z-index: 10;
    margin-top: -90px;
    background: #fff;
    padding: 3rem;
}

.fashion-conteiner>.post-wrapper .content h3 {
    margin-bottom: 1.5rem;
    line-height: 1.5;
    color: #000;
    font-weight: 300;
    font-family: "Playfair Display", Georgia, serif;
    font-size: 24px;
}

.fashion-conteiner>.post-wrapper .content p {
    font-size: 20px;
    line-height: 1.8;
    font-weight: 300;
    white-space: wrap;
    color: #737373;
}

.fashion-conteiner>.post-wrapper .content .btns {
    margin-top: 3rem;
}

.fashion-conteiner>.post-wrapper .content .btns button {
    background: #ee76ad;
    border: 1px solid #ee76ad;
    color: #fff;
    border-radius: 30px;
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
    line-height: 1.5;
}

.fashion-conteiner>.post-wrapper .content .btns button:hover {
    border: 1px solid #ee76ad;
    color: #ee76ad;
    background: transparent;
}