.pages{
    display: inline-block;
    margin: 0 2px;
    text-align: center;
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 50%;
    transition: .3s all ease;
    border: 1px solid #ccc;
}
.pages a{
    transition: .3s all ease;
    color: #ee76ad;
}
.pages.active a{
    color: #fff;
}
.pages.active{
    color: #fff;
    background: #ee76ad;
    border: 1px solid transparent;
}