@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Work Sans", Arial, sans-serif;
  font-weight: 300;
  color: #737373;
  font-size: 18px;
}

img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}
button, i, li{
  cursor: pointer;
}
input, textarea{
  outline: none;
}
a{
  text-decoration: none;
}
ul{
  list-style: none;
}
video{
  width: 100%;
  height: 100%;
}